.error-page {
  height: 100vh;
  padding: 50px;
  position: relative;
  background-blend-mode: luminosity;
  background-size: cover;
}
.error-page:before {
  content: "";
  position: absolute;
  opacity: 0.8;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.error-page .error-inner {
  z-index: 1;
  position: absolute;
  left: 50%;
  top: 50%;
  max-width: 600px;
  transform: translate(-50%, -50%);
  padding: 20px;
  width: 100%;
